"use client";

import { posicionMaiz } from '@/data/dashboardData';
import { useTheme } from '@/contexts/ThemeContext';
import { useCropFilter, getCropDisplayName } from '@/contexts/CropFilterContext';

export default function TreemapPosicionMaiz() {
  const { isDarkMode } = useTheme();
  const { selectedCrop } = useCropFilter();

  // Generate mock data for different crops
  const getCropPositionData = (cropName: string) => {
    const baseData = [
      { mes: "Agosto", valor: 1.5, color: "#10B981" },
      { mes: "Sep", valor: -0.4, color: "#EF4444" },
      { mes: "Oct", valor: 1.5, color: "#10B981" },
      { mes: "Diciembre", valor: 0.2, color: "#10B981" }
    ];

    // Modify values based on crop type for variety
    const cropMultipliers: { [key: string]: number[] } = {
      'Maíz': [1, 1, 1, 1],
      'Trigo': [0.8, 1.2, 0.9, 1.1],
      'Soja': [1.1, 0.7, 1.3, 0.8],
      'Girasol': [0.9, 1.1, 0.8, 1.2],
      'Sorgo': [1.2, 0.9, 1.1, 0.7],
      'Cebada': [0.7, 1.3, 0.9, 1.1]
    };

    const multipliers = cropMultipliers[cropName] || cropMultipliers['Maíz'];
    
    return baseData.map((item, index) => ({
      ...item,
      valor: parseFloat((item.valor * multipliers[index]).toFixed(1)),
      color: item.valor * multipliers[index] >= 0 ? "#10B981" : "#EF4444"
    }));
  };

  const currentCropName = selectedCrop ? getCropDisplayName(selectedCrop) : 'Maíz';
  const displayData = getCropPositionData(currentCropName);

  return (
    <div className={`${
      isDarkMode ? 'bg-gray-900 border-gray-800' : 'bg-white border-gray-200'
    } rounded-xl p-6 shadow-lg border transition-colors duration-200`}>
      <div className="flex items-center justify-between mb-4">
        <h3 className={`${isDarkMode ? 'text-white' : 'text-gray-900'} text-lg font-bold transition-colors duration-200`}>
          {currentCropName} - Posición negociada
        </h3>
        <span className={`${isDarkMode ? 'text-gray-400' : 'text-gray-600'} text-sm transition-colors duration-200`}>
          20/07 12:05
        </span>
      </div>

      {/* Treemap Grid */}
      <div className="grid grid-cols-2 gap-2 min-h-[280px]">
        {/* Agosto - Large block */}
        <div 
          className="row-span-2 rounded-lg p-4 flex flex-col justify-center items-center text-white font-bold relative overflow-hidden transition-all duration-300"
          style={{ backgroundColor: displayData[0].color }}
        >
          <div className="text-3xl font-black mb-2">Agosto</div>
          <div className="text-4xl font-black">
            {displayData[0].valor >= 0 ? '+' : ''}{displayData[0].valor.toFixed(1)}
          </div>
        </div>

        {/* Sep */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[1].color }}
        >
          <div className="text-xl font-bold">Sep</div>
          <div className="text-2xl font-black">
            {displayData[1].valor >= 0 ? '+' : ''}{displayData[1].valor.toFixed(1)}
          </div>
        </div>

        {/* Oct */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[2].color }}
        >
          <div className="text-xl font-bold">Oct</div>
          <div className="text-2xl font-black">
            {displayData[2].valor >= 0 ? '+' : ''}{displayData[2].valor.toFixed(1)}
          </div>
        </div>

        {/* Diciembre - Bottom row spanning both columns */}
        <div 
          className="col-span-2 rounded-lg p-3 flex items-center justify-between text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[3].color }}
        >
          <div className="text-xl font-bold">Diciembre</div>
          <div className="text-2xl font-black">
            {displayData[3].valor >= 0 ? '+' : ''}{displayData[3].valor.toFixed(1)}
          </div>
        </div>
      </div>
    </div>
  );
}
