"use client";

import { granosNegociados } from '@/data/dashboardData';
import { useTheme } from '@/contexts/ThemeContext';
import { useCropFilter, getCropDisplayName, normalizeCropName } from '@/contexts/CropFilterContext';

interface TreemapGranosNegociadosProps {
  data?: any[];
  loading?: boolean;
}

export default function TreemapGranosNegociados({ data, loading }: TreemapGranosNegociadosProps) {
  const { isDarkMode } = useTheme();
  const { selectedCrop } = useCropFilter();
  
  // Use real-time data if available, otherwise fall back to static data
  const displayData = data && data.length > 0 ? data : granosNegociados;

  // Calculate sizes based on absolute values for better visual representation
  const maxValue = Math.max(...displayData.map(g => Math.abs(g.variacion)));

  const itemsWithSizes = displayData.map(grano => {
    const isSelected = selectedCrop ? 
      normalizeCropName(grano.nombre) === normalizeCropName(getCropDisplayName(selectedCrop)) : 
      false;
    
    return {
      ...grano,
      size: Math.max((Math.abs(grano.variacion) / maxValue) * 40, 15), // Minimum size of 15%
      isSelected,
      // Enhance selected crop visibility
      color: isSelected ? (grano.variacion >= 0 ? '#059669' : '#DC2626') : grano.color,
      opacity: selectedCrop ? (isSelected ? 1 : 0.6) : 1
    };
  });

  // Sort to put selected crop first for better layout
  const sortedItems = [...itemsWithSizes].sort((a, b) => {
    if (a.isSelected) return -1;
    if (b.isSelected) return 1;
    return 0;
  });

  return (
    <div className={`${
      isDarkMode ? 'bg-gray-900 border-gray-800' : 'bg-white border-gray-200'
    } rounded-xl p-4 shadow-lg border transition-colors duration-200`}>
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <h3 className={`${isDarkMode ? 'text-white' : 'text-gray-900'} text-lg font-bold transition-colors duration-200`}>
          Granos negociados
          {loading && (
            <span className="ml-2 text-sm font-normal opacity-70">
              (Datos en tiempo real)
            </span>
          )}
        </h3>
        <span className={`${isDarkMode ? 'text-gray-400' : 'text-gray-600'} text-sm transition-colors duration-200`}>
          {new Date().toLocaleDateString('es-AR')} {new Date().toLocaleTimeString('es-AR', { hour: '2-digit', minute: '2-digit' })}
        </span>
      </div>
      
      {loading && (
        <div className="flex items-center justify-center py-4 mb-4">
          <div className={`${isDarkMode ? 'text-white' : 'text-gray-900'} flex items-center space-x-2`}>
            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-current"></div>
            <span className="text-sm">Actualizando variaciones...</span>
          </div>
        </div>
      )}
      
      {/* Treemap Grid */}
      <div className="grid grid-cols-3 gap-2 h-64">
        {/* First item - Large block (selected crop gets priority) */}
        <div 
          className={`col-span-2 row-span-2 rounded-lg p-4 flex flex-col justify-between text-white font-bold relative overflow-hidden transition-all duration-300 ${
            sortedItems[0]?.isSelected ? 'ring-2 ring-yellow-400 shadow-lg' : ''
          }`}
          style={{ 
            backgroundColor: sortedItems[0]?.color || '#10B981',
            opacity: sortedItems[0]?.opacity || 1
          }}
        >
          <div className="text-2xl font-black">{sortedItems[0]?.nombre || 'Maíz'}</div>
          <div className="text-3xl font-black">
            {(sortedItems[0]?.variacion || 0) >= 0 ? '+' : ''}{(sortedItems[0]?.variacion || 0).toFixed(1)}
          </div>
          {sortedItems[0]?.isSelected && (
            <div className="absolute top-2 right-2">
              <div className="w-3 h-3 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
          )}
        </div>

        {/* Second item */}
        <div 
          className={`rounded-lg p-3 flex flex-col justify-between text-white font-bold transition-all duration-300 ${
            sortedItems[1]?.isSelected ? 'ring-2 ring-yellow-400 shadow-lg' : ''
          }`}
          style={{ 
            backgroundColor: sortedItems[1]?.color || '#6B7280',
            opacity: sortedItems[1]?.opacity || 1
          }}
        >
          <div className="text-lg font-bold">{sortedItems[1]?.nombre || 'Trigo'}</div>
          <div className="text-xl font-black">
            {(sortedItems[1]?.variacion || 0) >= 0 ? '+' : ''}{(sortedItems[1]?.variacion || 0).toFixed(1)}
          </div>
          {sortedItems[1]?.isSelected && (
            <div className="absolute top-1 right-1">
              <div className="w-2 h-2 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
          )}
        </div>

        {/* Third item */}
        <div 
          className={`rounded-lg p-3 flex flex-col justify-between text-white font-bold transition-all duration-300 ${
            sortedItems[2]?.isSelected ? 'ring-2 ring-yellow-400 shadow-lg' : ''
          }`}
          style={{ 
            backgroundColor: sortedItems[2]?.color || '#EF4444',
            opacity: sortedItems[2]?.opacity || 1
          }}
        >
          <div className="text-lg font-bold">{sortedItems[2]?.nombre || 'Soja'}</div>
          <div className="text-xl font-black">
            {(sortedItems[2]?.variacion || 0) >= 0 ? '+' : ''}{(sortedItems[2]?.variacion || 0).toFixed(1)}
          </div>
          {sortedItems[2]?.isSelected && (
            <div className="absolute top-1 right-1">
              <div className="w-2 h-2 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
          )}
        </div>

        {/* Fourth item */}
        <div 
          className={`rounded-lg p-3 flex flex-col justify-between text-white font-bold transition-all duration-300 ${
            sortedItems[3]?.isSelected ? 'ring-2 ring-yellow-400 shadow-lg' : ''
          }`}
          style={{ 
            backgroundColor: sortedItems[3]?.color || '#EF4444',
            opacity: sortedItems[3]?.opacity || 1
          }}
        >
          <div className="text-sm font-bold">{sortedItems[3]?.nombre || 'Sorgo'}</div>
          <div className="text-lg font-black">
            {(sortedItems[3]?.variacion || 0) >= 0 ? '+' : ''}{(sortedItems[3]?.variacion || 0).toFixed(1)}
          </div>
          {sortedItems[3]?.isSelected && (
            <div className="absolute top-1 right-1">
              <div className="w-2 h-2 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
          )}
        </div>

        {/* Fifth item */}
        <div 
          className={`rounded-lg p-3 flex flex-col justify-between text-white font-bold transition-all duration-300 ${
            sortedItems[4]?.isSelected ? 'ring-2 ring-yellow-400 shadow-lg' : ''
          }`}
          style={{ 
            backgroundColor: sortedItems[4]?.color || '#10B981',
            opacity: sortedItems[4]?.opacity || 1
          }}
        >
          <div className="text-sm font-bold">{sortedItems[4]?.nombre || 'Cebada'}</div>
          <div className="text-lg font-black">
            {(sortedItems[4]?.variacion || 0) >= 0 ? '+' : ''}{(sortedItems[4]?.variacion || 0).toFixed(1)}
          </div>
          {sortedItems[4]?.isSelected && (
            <div className="absolute top-1 right-1">
              <div className="w-2 h-2 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
          )}
        </div>

        {/* Sixth item */}
        <div 
          className={`rounded-lg p-3 flex flex-col justify-between text-white font-bold transition-all duration-300 ${
            sortedItems[5]?.isSelected ? 'ring-2 ring-yellow-400 shadow-lg' : ''
          }`}
          style={{ 
            backgroundColor: sortedItems[5]?.color || '#EF4444',
            opacity: sortedItems[5]?.opacity || 1
          }}
        >
          <div className="text-sm font-bold">{sortedItems[5]?.nombre || 'Girasol'}</div>
          <div className="text-lg font-black">
            {(sortedItems[5]?.variacion || 0) >= 0 ? '+' : ''}{(sortedItems[5]?.variacion || 0).toFixed(1)}
          </div>
          {sortedItems[5]?.isSelected && (
            <div className="absolute top-1 right-1">
              <div className="w-2 h-2 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
