"use client";

import { useTheme } from '@/contexts/ThemeContext';
import { useCurrency } from '@/contexts/CurrencyContext';

export default function CurrencyToggle() {
  const { isDarkMode } = useTheme();
  const { isUSD, exchangeRate, toggleCurrency } = useCurrency();

  return (
    <button
      onClick={toggleCurrency}
      className={`flex items-center space-x-2 px-4 py-2 rounded-lg font-medium text-sm transition-all duration-200 ${
        isDarkMode 
          ? 'bg-gray-800 hover:bg-gray-700 text-white border border-gray-700' 
          : 'bg-white hover:bg-gray-50 text-gray-900 border border-gray-200 shadow-sm'
      }`}
      title={`Cambiar a ${isUSD ? 'Pesos Argentinos' : 'Dólares'} (Cotización oficial: $${exchangeRate.toFixed(2)})`}
    >
      <span className={`font-bold ${isUSD ? 'text-green-600' : 'text-blue-600'}`}>
        {isUSD ? 'USD' : 'ARS'}
      </span>
      <span className="text-xs opacity-70">
        {isUSD ? `$${exchangeRate.toFixed(0)}` : '1:1'}
      </span>
      <svg 
        className={`w-4 h-4 transition-transform duration-200 ${isUSD ? 'rotate-180' : ''}`} 
        fill="none" 
        stroke="currentColor" 
        viewBox="0 0 24 24"
      >
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16V4m0 0L3 8m4-4l4 4m6 0v12m0 0l4-4m-4 4l-4-4" />
      </svg>
    </button>
  );
}

export { CurrencyToggle };
