"use client";

import DashboardHeader from '@/components/DashboardHeader';
import OptimusBanner from '@/components/OptimsBanner';
import FiltersSection from '@/components/FiltersSection';
import PriceCards from '@/components/PriceCards';
import TreemapGranosNegociados from '@/components/TreemapGranosNegociados';
import TreemapPosicionMaiz from '@/components/TreemapPosicionMaiz';
import TreemapZonaDestino from '@/components/TreemapZonaDestino';
import { useTheme } from '@/contexts/ThemeContext';
import { useRealTimeData } from '@/hooks/useRealTimeData';
import { useCropFilter, getCropDisplayName } from '@/contexts/CropFilterContext';

export default function Dashboard() {
  const { isDarkMode } = useTheme();
  const { selectedCrop } = useCropFilter();
  const { prices, granosNegociados, loading, error, lastUpdate, refreshData } = useRealTimeData(selectedCrop);

  return (
    <div className={`min-h-screen transition-colors duration-200 ${isDarkMode ? 'bg-[#121212]' : 'bg-gray-50'}`}>
      {/* Header */}
      <DashboardHeader />
      
      {/* Banner Optimus */}
      <OptimusBanner />
      
      {/* Filtros */}
      <FiltersSection />
      
      {/* Status bar */}
      <div className={`${isDarkMode ? 'bg-gray-800 text-white' : 'bg-gray-100 text-gray-900'} px-6 py-2 text-sm transition-colors duration-200`}>
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
              loading ? 'bg-yellow-100 text-yellow-800' : 
              error ? 'bg-red-100 text-red-800' : 'bg-green-100 text-green-800'
            }`}>
              {loading ? '🔄 Actualizando...' : error ? '⚠️ Error en datos' : '✅ Datos actualizados'}
            </span>
            <span className="opacity-70">
              Cultivo: {getCropDisplayName(selectedCrop)}
            </span>
            {lastUpdate && (
              <span className="opacity-70">
                Última actualización: {lastUpdate.toLocaleString('es-AR')}
              </span>
            )}
          </div>
          <button
            onClick={refreshData}
            className={`px-3 py-1 rounded text-xs font-medium transition-colors duration-200 ${
              isDarkMode ? 'bg-gray-700 hover:bg-gray-600 text-white' : 'bg-white hover:bg-gray-50 text-gray-900'
            }`}
            disabled={loading}
          >
            {loading ? 'Actualizando...' : 'Actualizar ahora'}
          </button>
        </div>
      </div>

      {/* Contenido principal */}
      <div className="flex gap-6 px-6 pb-6">
        {/* Panel izquierdo - Tarjetas de precios */}
        <div className="flex-1">
          <PriceCards prices={prices} loading={loading} />
        </div>

        {/* Panel derecho - Gráficos Treemap */}
        <div className="w-96 space-y-12 mt-6">
          <TreemapGranosNegociados data={granosNegociados} loading={loading} />
          <TreemapPosicionMaiz />
          <TreemapZonaDestino />
        </div>
      </div>
      
      {/* Footer opcional */}
      <footer className={`${isDarkMode ? 'bg-[#121212] border-gray-800' : 'bg-white border-gray-200'} border-t px-6 py-4 mt-8 transition-colors duration-200`}>
        <div className="flex items-center justify-between">
          <div className={`${isDarkMode ? 'text-gray-400' : 'text-gray-600'} text-sm transition-colors duration-200`}>
            © 2024 Intagro - Optimus Dashboard
          </div>
          <div className={`${isDarkMode ? 'text-gray-400' : 'text-gray-600'} text-sm transition-colors duration-200`}>
            Versión 1.0.0 - Datos en tiempo real
          </div>
        </div>
      </footer>
    </div>
  );
}
