"use client";

import { zonasDestino } from '@/data/dashboardData';
import { useTheme } from '@/contexts/ThemeContext';
import { useCropFilter, getCropDisplayName } from '@/contexts/CropFilterContext';

export default function TreemapZonaDestino() {
  const { isDarkMode } = useTheme();
  const { selectedCrop } = useCropFilter();

  // Generate mock data for different crops
  const getCropDestinationData = (cropName: string) => {
    const baseData = [
      { nombre: "Rosario Total", valor: 1.9, color: "#10B981" },
      { nombre: "BB", valor: 0.7, color: "#10B981" },
      { nombre: "QQ", valor: 1.1, color: "#10B981" },
      { nombre: "Lima", valor: 0.8, color: "#10B981" },
      { nombre: "Ros Norte", valor: 0.7, color: "#10B981" },
      { nombre: "Ros Sur", valor: 0.7, color: "#10B981" },
      { nombre: "TLP", valor: -0.2, color: "#6B7280" },
      { nombre: "Espartillar", valor: -0.3, color: "#EF4444" }
    ];

    // Modify values based on crop type for variety
    const cropMultipliers: { [key: string]: number[] } = {
      'Maíz': [1, 1, 1, 1, 1, 1, 1, 1],
      'Trigo': [0.9, 1.1, 0.8, 1.2, 0.9, 1.1, 1.3, 0.8],
      'Soja': [1.2, 0.8, 1.1, 0.9, 1.3, 0.7, 0.9, 1.2],
      'Girasol': [0.8, 1.2, 0.9, 1.1, 0.8, 1.2, 1.1, 0.9],
      'Sorgo': [1.1, 0.9, 1.2, 0.8, 1.1, 0.9, 0.8, 1.3],
      'Cebada': [0.7, 1.3, 0.9, 1.1, 0.8, 1.2, 1.2, 0.8]
    };

    const multipliers = cropMultipliers[cropName] || cropMultipliers['Maíz'];
    
    return baseData.map((item, index) => ({
      ...item,
      valor: parseFloat((item.valor * multipliers[index]).toFixed(1)),
      color: item.valor * multipliers[index] >= 0 ? "#10B981" : (item.valor * multipliers[index] >= -0.1 ? "#6B7280" : "#EF4444")
    }));
  };

  const currentCropName = selectedCrop ? getCropDisplayName(selectedCrop) : 'Maíz';
  const displayData = getCropDestinationData(currentCropName);

  return (
    <div className={`${
      isDarkMode ? 'bg-gray-900 border-gray-800' : 'bg-white border-gray-200'
    } rounded-xl p-4 shadow-lg border transition-colors duration-200`}>
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <h3 className={`${isDarkMode ? 'text-white' : 'text-gray-900'} text-lg font-bold transition-colors duration-200`}>
          {currentCropName} - Zona de destino
        </h3>
        <span className={`${isDarkMode ? 'text-gray-400' : 'text-gray-600'} text-sm transition-colors duration-200`}>
          20/07 12:05
        </span>
      </div>
      
      {/* Treemap Grid */}
      <div className="grid grid-cols-3 gap-2 min-h-[280px]">
        {/* Rosario Total - Large block */}
        <div 
          className="col-span-2 row-span-2 rounded-lg p-4 flex flex-col justify-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[0].color }}
        >
          <div className="text-2xl font-black mb-2">Rosario</div>
          <div className="text-2xl font-black mb-1">Total</div>
          <div className="text-3xl font-black">
            {displayData[0].valor >= 0 ? '+' : ''}{displayData[0].valor.toFixed(1)}
          </div>
        </div>

        {/* BB */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[1].color }}
        >
          <div className="text-lg font-bold">BB</div>
          <div className="text-xl font-black">
            {displayData[1].valor >= 0 ? '+' : ''}{displayData[1].valor.toFixed(2)}
          </div>
        </div>

        {/* QQ */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[2].color }}
        >
          <div className="text-lg font-bold">QQ</div>
          <div className="text-xl font-black">
            {displayData[2].valor >= 0 ? '+' : ''}{displayData[2].valor.toFixed(1)}
          </div>
        </div>

        {/* Lima */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[3].color }}
        >
          <div className="text-sm font-bold">Lima</div>
          <div className="text-lg font-black">
            {displayData[3].valor >= 0 ? '+' : ''}{displayData[3].valor.toFixed(1)}
          </div>
        </div>

        {/* Ros Norte */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[4].color }}
        >
          <div className="text-xs font-bold">Ros Norte</div>
          <div className="text-lg font-black">
            {displayData[4].valor >= 0 ? '+' : ''}{displayData[4].valor.toFixed(1)}
          </div>
        </div>

        {/* TLP */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[6].color }}
        >
          <div className="text-sm font-bold">TLP</div>
          <div className="text-lg font-black">
            {displayData[6].valor >= 0 ? '+' : ''}{displayData[6].valor.toFixed(1)}
          </div>
        </div>

        {/* Ros Sur */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[5].color }}
        >
          <div className="text-xs font-bold">Ros Sur</div>
          <div className="text-lg font-black">
            {displayData[5].valor >= 0 ? '+' : ''}{displayData[5].valor.toFixed(1)}
          </div>
        </div>

        {/* Espartillar */}
        <div 
          className="rounded-lg p-3 flex flex-col justify-center items-center text-white font-bold transition-all duration-300"
          style={{ backgroundColor: displayData[7].color }}
        >
          <div className="text-xs font-bold">Espartillar</div>
          <div className="text-sm font-black">
            {displayData[7].valor >= 0 ? '+' : ''}{displayData[7].valor.toFixed(1)}
          </div>
        </div>
      </div>
    </div>
  );
}
