"use client";

import { useState, useEffect, useCallback } from 'react';
import { fetchPreciosFOB, calculateVariations, mapProductsToLocations, PrecioFOB } from '@/services/apiService';
import { normalizeCropName, getCropDisplayName } from '@/contexts/CropFilterContext';

export interface RealTimeData {
  prices: any[];
  granosNegociados: any[];
  loading: boolean;
  error: string | null;
  lastUpdate: Date | null;
}

export const useRealTimeData = (selectedCrop?: string) => {
  const [data, setData] = useState<RealTimeData>({
    prices: [],
    granosNegociados: [],
    loading: true,
    error: null,
    lastUpdate: null,
  });

  const fetchData = useCallback(async () => {
    try {
      setData(prev => ({ ...prev, loading: true, error: null }));
      
      console.log('Fetching real-time agricultural data...');
      if (selectedCrop) {
        console.log(`Filtering data for selected crop: ${getCropDisplayName(selectedCrop)}`);
      }
      
      // Fetch prices with variations
      const pricesWithVariations = await calculateVariations();
      
      // Filter by selected crop if specified
      let filteredPrices = pricesWithVariations;
      if (selectedCrop) {
        const normalizedSelectedCrop = normalizeCropName(getCropDisplayName(selectedCrop));
        filteredPrices = pricesWithVariations.filter(price => 
          normalizeCropName(price.producto) === normalizedSelectedCrop
        );
        
        console.log(`Filtered ${filteredPrices.length} prices for ${getCropDisplayName(selectedCrop)}`);
      }
      
      // Map to location-based prices for cards (filtered by crop)
      const locationPrices = mapProductsToLocations(filteredPrices);
      
      // Transform for treemap (granos negociados) - show all crops with selected one highlighted
      const granosNegociados = pricesWithVariations.map(price => ({
        nombre: price.producto,
        variacion: price.variacion || 0,
        color: (price.variacion || 0) >= 0 ? '#10B981' : '#EF4444',
        isSelected: selectedCrop ? normalizeCropName(price.producto) === normalizeCropName(getCropDisplayName(selectedCrop)) : false
      }));

      setData({
        prices: locationPrices,
        granosNegociados,
        loading: false,
        error: null,
        lastUpdate: new Date(),
      });

      console.log('Data updated successfully:', {
        pricesCount: locationPrices.length,
        granosCount: granosNegociados.length,
        selectedCrop: selectedCrop ? getCropDisplayName(selectedCrop) : 'All',
        timestamp: new Date().toLocaleString('es-AR')
      });

    } catch (error) {
      console.error('Error fetching real-time data:', error);
      setData(prev => ({
        ...prev,
        loading: false,
        error: error instanceof Error ? error.message : 'Error desconocido',
      }));
    }
  }, [selectedCrop]);

  useEffect(() => {
    // Initial fetch
    fetchData();

    // Set up automatic updates every 5 minutes (300,000 ms)
    const interval = setInterval(() => {
      console.log('Auto-updating data (5 minute interval)...');
      fetchData();
    }, 5 * 60 * 1000);

    // Cleanup interval on unmount
    return () => {
      clearInterval(interval);
      console.log('Real-time data updates stopped');
    };
  }, [fetchData]);

  // Re-fetch data when selected crop changes
  useEffect(() => {
    if (selectedCrop) {
      console.log(`Crop selection changed to: ${getCropDisplayName(selectedCrop)}`);
      fetchData();
    }
  }, [selectedCrop, fetchData]);

  // Manual refresh function
  const refreshData = useCallback(() => {
    console.log('Manual data refresh triggered');
    fetchData();
  }, [fetchData]);

  return {
    ...data,
    refreshData,
  };
};

// Hook for individual price monitoring with crop filtering
export const usePriceMonitoring = (producto: string) => {
  const [price, setPrice] = useState<PrecioFOB | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchPrice = async () => {
      try {
        const result = await fetchPreciosFOB();
        if (result.success) {
          const productPrice = result.data.find(
            p => p.producto.toLowerCase() === producto.toLowerCase()
          );
          setPrice(productPrice || null);
        }
      } catch (error) {
        console.error(`Error fetching price for ${producto}:`, error);
      } finally {
        setLoading(false);
      }
    };

    fetchPrice();
    const interval = setInterval(fetchPrice, 5 * 60 * 1000); // 5 minutes

    return () => clearInterval(interval);
  }, [producto]);

  return { price, loading };
};
